import { useMemo } from 'react'

const useArrayIndex = (keyField, array = []) => {

  if(
    !keyField ||
    typeof keyField !== 'string' ||
    keyField.constructor !== Function
  ) throw new Error("invalid 'keyField' in useArrayIndex. It should be a string or a function returning a string")

  const key = useMemo(() => {
    return typeof keyField === 'string' ? keyField : keyField()
  },[ keyField ])

  if(typeof key !== 'string')
    throw new Error("'keyField' in useArrayIndex did not return string")

  const index = useMemo(() => {
    return array.reduce((a,b) => {
      a[b[key]] = b
      return a
    },{})
  },[ key, array ])

  return index

}

export default useArrayIndex
